	module.exports = {"data":{"post":{"html":"<p>In Seattle they blow up old stadiums after they build new ones. In Hamburg, they keep playing in the old <a href=\"http://www.stevie-online.de/stadion/bilder1.html\">Volksparkstadion</a> even while it's being rebuilt. Over the summer, they twisted the field 90 degrees so that the old end zone seats moved to midfield. This week they are starting on the roof fabric... several weeks behind schedule. (Best of all, the club is paying for the new stadium. Having taxpayers pay for sports stadiums is an American idocy.)</p>\n<p><a href=\"http://scriptingnews.userland.com/backissues/2000/04/03\">Scripting News</a>:  \"Now the timezone of your Manila site is a preference.\" <i>Thank you, thank you!</i></p>","fields":{"title":"Tuesday","date":"04 Apr 2000","url":"https://papascott.de/2000-04-04-tuesday/"}}},"pathContext":{"slug":"/2000-04-04-tuesday/","prev":{"fields":{"title":"Baptism Party","date":"03 Apr 2000","slug":"/2000-04-03-baptism-party/"}},"next":{"fields":{"title":"Let there be light","date":"08 Apr 2000","slug":"/2000-04-08-let-there-be-light/"}}}}

